body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
}

html::after {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	opacity: 0;
	pointer-events: none;
	background-image: -webkit-repeating-linear-gradient(
		top,
		#000,
		#000 1px,
		transparent 1px,
		transparent 1em
	);
	background-image: -o-repeating-linear-gradient(
		top,
		#000,
		#000 1px,
		transparent 1px,
		transparent 1em
	);
	background-image: repeating-linear-gradient(
		to bottom,
		#000,
		#000 1px,
		transparent 1px,
		transparent 1em
	);
	background-position-y: -1px;
}



